import React from 'react'
import { Route, Switch } from 'react-router'
import Foo from './Components/Foo';
import Manager from './Components/Manager';
import Test from './Components/Test';
import Users from './Components/Configuration/Users';

const routes = (
  <div>
    <Switch>
      <Route path='/management' component={() => <Manager name='Manager'/>} />
      <Route path='/operation' component={() => <Test name='Test'/>} />
      <Route path='/location_master' component={() => <Foo name='Location Master'/>} />
      <Route path='/route_master' component={() => <Foo name='Route Master'/>} />
      <Route path='/hazard_point' component={() => <Foo name='Hazard Points'/>} />
      <Route path='/users' component={() => <Users name='Users'/>} />
      <Route path='/settings' component={() => <Foo name='Settings'/>} />
    </Switch>
  </div>
)

export default routes
